@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap);
body {
  font-family: "Roboto", arial, sans-serif;
  overflow-x: hidden;
  font-size: 14px;
}

* {
  margin: 0;
  padding: 0;
}

.app {
  display: flex;
}

.home {
  color: #fff;
  background-color: #181818;
}

.watch--x {
  overflow-y: auto;
}
.header {
    display: flex;
    justify-content: space-between;
    background-color: #202020;
    height: max(10vh);
  }
  .header__left,
.header__right {
  display: flex;
  align-items: center;
}
.header__logo {
  width: max(77px);
  height: max(77px);
}
.header__menuicon {
  padding: 20px;
}
.header__right > * {
  cursor: pointer;
  margin-right: 20px;
  font-size: 25px !important;
}
.header__center {
  display: flex;
  align-items: center;
  flex-grow: 1;
  justify-content: center;
}
.header__input {
  width: max(50%);
  height: 32px;
  background-color: #121212;
  padding-left: 10px;
  outline: none;
  border: none;
  border: 1px solid #303030;
  border-right: none;
  font-size: 16px;
}
.header__btn {
  height: 33.7px;
  background-color: #313131 !important;
  border-radius: 0 !important;
}
.header__searchIcon {
  opacity: 0.2;
  color: #fff;
}

.MuiPopover-paper {
  max-height: calc(100vh - 62px - 100px);
  width: 354px;
  margin-top: 20px;
  margin-right: -10px;
  height: 390px;
}

.home__popoverContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.home__popover__top {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-bottom: 1px solid #dadce0;
  width: 120%;
  height: 190px;
}

.home__badge {
  background: #fff;
  color: #5f6368;
  border-radius: 50%;
  width: 30px;
  height: 30px;
}
.home__camera {
  width: 17px !important;
  height: 17px !important;
  margin: 4px;
  box-shadow: 0px 1px 1px 0 rgba(65, 69, 73, 0.3),
    0px 1px 3px 1px rgba(65, 69, 73, 0.15);
  border-radius: 50%;
  padding: 5px;
  cursor: pointer;
}
.home__camera:hover {
  color: #6ba4f1;
}

.home__text {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 15px;
  color: #202124;
}

.home__displayName {
  font-family: "Poppins", sans-serif;
  color: #202124;
  font-weight: 500;
  letter-spacing: -0.2px;
  text-transform: capitalize;
}

.home__mail {
  font-family: "Roboto", sans-serif;
  color: #5f6368;
}

.home__btn {
  background-color: #ffffff;
  border: 1px solid #dadce0;
  border-radius: 100px;
  color: #3c4043;
  margin: 16px 0 0;
  max-width: 254px;
  padding: 8px 16px;
  font-family: "Poppins";
  cursor: pointer;
  -webkit-user-select: none;
          user-select: none;
}

.home__btn:hover {
  background-color: #f7f8f8;
}

.home__popover__btm {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.home__addBtn {
  display: flex;
  align-items: center;
  width: 354px;
  border-bottom: 1px solid #dadce0;
  color: #5f6368;
  cursor: pointer;
  font-family: "Poppins";
  padding: 10px;
}

.home__addBtn:hover {
  background-color: #f7f8f8;
}

.home__addIcon {
  margin-left: 40px;
  margin-right: 20px;
}

.home__signOut {
  margin-top: 20px !important;
  color: #5f6368 !important;
  text-transform: capitalize !important;
}

.home__popover__footer {
  display: flex;
  position: absolute;
  bottom: 0;
  width: 120%;
  align-items: center;
  color: #5f6368;
  justify-content: center;
  border-top: 1px solid #dadce0;
  padding-top: 5px;
}

.home__popover__footer > span {
  margin: 10px;
}

.Logo {
  display: flex;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
  font-weight: bold;
  size: 45;
};

.search-box{
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: #34495e;
  height: 40px;
  border-radius: 40px;
  padding: 10px;
  display: flex;
  flex-direction: row;
}
.search-box:hover > .search-txt{
  width: 240px;
  
  padding: 0 6px;
  }
.search-box:hover > .search-btn{
  background : white ;
  
  }
.search-btn{
  color: #e74c3c;
  float: right;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background:#0000ff;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  overflow: hidden;
}
.search-txt{
  border:none;
  background: none;
  outline: none;
  float: left;
  padding: 0;
  color: white;
  font-size: 16px;
  transition: 0.4s;
  line-height: 40px;
  width: 0px;  
}
/* responsive*/
@media only screen and (max-width: 828px){
 .search-box:hover > .search-txt{
    width: 125px;
    }
}


.sidebar {
    width:  max(68px);
    background-color: #202020;
    height: 91.5vh;
  }
  .sidebar__btn {
    display: flex;
    padding: 8px 20px;
    cursor: pointer;
    align-items: center;
  }
  .sidebar__btn > .sidebar__icon {
    opacity: 0.5;
  }
  .sidebar__btn > p {
    opacity: 0.9;
    font-weight: 400 !important;
    font-size: 0.9rem;
  }
  .sidebar__btn--active > .sidebar__icon,
  .sidebar__btn--active > p {
    opacity: 1 !important;
  }
  .sidebar__btn--active,
  .sidebar__btn:hover {
    background-color: rgba(37, 85, 241, 0.486);
  }
  .sidebar__icon {
    margin-right: 20px;
  }
  .sidebar__buttons {
    padding-bottom: 20px;
    border-bottom: 1px solid #303030;
  }
  .bottom {
    margin-top: 10px;
  }
  
  .desc {
    font-size: 12px;
    margin-bottom: 10px;
  }

  .Sidebar_brandicon{
    font-size: 2rem;
  }

  
 
.main {
    display: flex;
    flex-wrap: wrap;
    overflow-y: auto;
    max-height: 90vh;
    width: 100%;
    overflow-x: hidden;
  }
  .videothumb__thumbnail {
    width: inherit;
    height: 137px;
    margin-bottom: 10px;
  }
  .videothumb__details {
    display: flex;
  }
  .videothumb {
    margin: 20px;
    margin-right: 0;
    width: 230px;
    height: 236px;
    cursor: pointer;
  }
  .videothumb__title {
    font-size: 0.9rem;
    max-height: 42px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: wrap;
    font-weight: 500;
    letter-spacing: 0.1px;
  }
  .videothumb__channel {
    margin-left: 10px;
  }
  .videothumb__texts {
    margin-top: 5px;
  }
  .videothumb__text {
    font-size: 14px;
    color: #aaaaaa !important;
  }



  .listItem {
    width: 225px;
    height: 120px;
    background-color: var(--main-color);
    margin-right: 5px;
    overflow: hidden;
    cursor: pointer;
    color: white;
  }

  .itemInfo {
    display: flex;
    flex-direction: column;
    padding: 5px;
  }
  .icons {
    display: flex;
    margin-bottom: 10px;
  }

  .icon {
    border: 2px solid white;
    padding: 5px;
    border-radius: 50%;
    margin-right: 10px;
    font-size: 16px;
  }

  .itemInfoTop {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    font-size: 14px;
    font-weight: 600;
    color: gray;
  }

  .limit {
    border: 1px solid gray;
    padding: 1px 3px;
    margin: 0 10px;
  }

  .desc {
    font-size: 12px;
    margin-bottom: 10px;
  }

  .subBtn {
    height: 40px !important;
  }
.channel__art {
    width: 100%;
    height: 250px;
  }
  .channel__avatar {
    width: 80px !important;
    height: 80px !important;
    margin-right: 20px;
  }
  .channel__details {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
  }
  
  .channel__avatarWrap {
    display: flex;
    align-items: center;
  }
  .channel__title {
    font-size: 26px !important;
    font-weight: normal !important;
  }
  .channel__content {
    height: 100%;
    background-color: #0f0f0f;
    overflow-y: auto;
  }
  .channel__detailsWrap {
    display: flex;
    justify-content: space-between;
    width: 80%;
    align-items: center;
  }
  .channel__subBtn {
    height: 40px !important;
  }
  .channel__links {
    display: flex;
    width: 50%;
    margin-right: 300px;
    margin-top: 20px;
    justify-content: space-between;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
  }
  
  .channel__link > p {
    color: white;
    opacity: 0.5;
    font-weight: 500;
    cursor: pointer;
    font-size: 15px;
  }
  .channel__link:hover > p {
    opacity: 1;
  }
  
  .channel__link--active {
    height: 20px;
  }
  
  .channel__link__border {
    background-color: #fff;
    height: 2px;
    margin-top: 10px;
    opacity: 0.6;
  }
  .channel__link--active > p {
    opacity: 1 !important;
  }
  .channel__contentWrapper {
    margin-left: 140px;
    padding-top: 20px;
    display: flex;
    flex-wrap: wrap;
  }
  .channel {
    overflow-y: scroll;
  }
